import React, { useEffect, useState } from "react";
import { Form, Select, Input,Modal, DatePicker,TimePicker } from "antd";
import { connect } from "react-redux";
import moment from "moment";
import actionDmDungCu from "@actions/dm-dung-cu";
import actionDepartments from '@actions/departments';
import actionQuanLyDoVai from '@actions/quan-ly-do-vai';
import DataContants from '@config/data-contants';
import '../style.scss';
const { Option } = Select;
function index(props){

  useEffect(() => {
    props.loadListDepartments();
    props.loadListDmDungcu("","","DO_VAI");
    props.updateData({
      checkValidate:false,
    })
  }, []);

  const handleSubmit = e => {
    if(!props.id){
    if((props.dmDungCuId!=="") && (props.khoaBanGiaoId!=="")
    && (props.soLuong!=="") && props.soLuong>0
    && (props.thoiGianBanGiao && props.thoiGianThucHien ? (moment(props.thoiGianBanGiao).format("YYYYMMDD") <= moment(props.thoiGianThucHien).format("YYYYMMDD")) : true)
    ){
      props.updateData({
        checkValidate: false
      })
      props.createOrEdit().then(s => {})
    } else {
      props.updateData({
        checkValidate: true
      })
      return
    }
  } else if((props.trangThai!=="") && (props.dmDungCuId!=="") && (props.khoaBanGiaoId!=="")
  && (props.soLuong!=="") && props.soLuong>0
  && (props.thoiGianBanGiao && props.thoiGianThucHien ? (moment(props.thoiGianBanGiao).format("YYYYMMDD") <= moment(props.thoiGianThucHien).format("YYYYMMDD")) : true)){
    props.updateData({checkValidate:false})
    props.createOrEdit().then(s=>{})
  } else {
    props.updateData({checkValidate:true})
    return
  }
  };

  const { getFieldDecorator } = props.form;

  const closeModal = () => {
    props.updateData({
      isOpen: false,
      id: "",
      dmDungCuId: "",
      soLuong: "",
      khoaBanGiaoId:"",
      trangThai:"",
      thoiGianThucHien:"",
      thoiGianBanGiao: "",
    })
  }
  return (
    <Modal
      className="modal-popup"
      width={500}
      title={props.id ? "Cp nht danh sch thanh trng  vi" : "Thm mi danh sch thanh trng  vi"}
      visible={true}
      cancelText={"ng"}
      onOk={handleSubmit } 
      okText={props.id ? "Cp nht" : "Thm mi"}
      onCancel={closeModal}
    >
      <Form layout="vertical" hideRequiredMark={true} onSubmit={handleSubmit}>
        <div>
          <Form.Item label="Tn sn phm* ">
              <Select
              value={props.dmDungCuId}
              showSearch
              onChange={e =>{
                props.updateData({
                  dmDungCuId: e
                });
              }}
              filterOption={(input, option) =>
                option.props.children
                  .toLowerCase().unsignText()
                  .indexOf(input.toLowerCase().unsignText()) >= 0
              }
              >
              <Option value="">Chn sn phm</Option>
              {props.dmDungCu.map((item, index) => {
                return (
                  <Option key={index} value={item.id}>
                    {item.name}
                  </Option>
                );
              })}
              </Select>
              {props.checkValidate && !props.dmDungCuId ? <div className="validate">Vui lng chn tn sn phm</div> : null}
          </Form.Item>
          <Form.Item label="S lng* ">
            <Input
              value={props.soLuong}
              type="number"
              onInput={(e) => e.target.value = e.target.value.slice(0, 5)}
              onChange={(event) => {
                props.updateData({
                  soLuong: event.target.value
                });
              }}
              placeholder="Nhp s lng"
              />
              {props.checkValidate && !props.soLuong ? <div className="validate">Vui lng nhp s lng sn phm</div> : 
                props.checkValidate && (props.soLuong && props.soLuong<=0 ? <div className="validate">S lng phi ln hn 0</div> : null)}
          </Form.Item>
          <Form.Item label="Thi gian nhn* ">
              <DatePicker
              value={props.thoiGianBanGiao}
              style={{width:"100%"}}
              onChange={(event) => {
                props.updateData({
                  thoiGianBanGiao: event
                });
              }}
              format="DD/MM/YYYY"
              placeholder="Chn thi gian nhn"
            />
            {props.checkValidate && !props.thoiGianBanGiao ? <div className="validate">Vui lng chn thi gian nhn</div> : null}
          </Form.Item>
          <Form.Item label="Khoa bn giao*">
            <Select
              showSearch
              value={props.khoaBanGiaoId}
              onChange={e =>{
                props.updateData({
                  khoaBanGiaoId: e
                });
              }}
              filterOption={(input, option) =>
                option.props.children
                  .toLowerCase().unsignText()
                  .indexOf(input.toLowerCase().unsignText()) >= 0
              }
            >
              <Option value="">Chn khoa bn giao</Option>
                {props.departments.map((item, index) => {
                  return (
                    <Option key={index} value={item.id}>
                      {item.name}
                    </Option>
                  );
                })}
            </Select>
            {props.checkValidate && !props.khoaBanGiaoId ? <div className="validate">Vui lng chn khoa bn giao</div> : null}
          </Form.Item>
        </div>
        <div>
          <Form.Item style={ props.id ? {} : {display:"none"}} label="Trng thi*">
          <Select    
              showSearch
              onChange={e =>{
                props.updateData({
                  trangThai: e
                });
              }}
              value={props.trangThai}
              filterOption={(input, option) =>
                option.props.children
                  .toLowerCase().unsignText()
                  .indexOf(input.toLowerCase().unsignText()) >= 0
              }
            >
              <Option value="">Chn trng thi</Option>
              {DataContants.listTrangThai.map((item, index) => {
                      return (
                        <Option key={index} value={item.id}>
                          {item.name}
                        </Option>
                      );
                    })}
            </Select>
            {props.checkValidate && !props.trangThai ? <div className="validate">Vui lng chn trng thi</div> : null}
          </Form.Item>
          <Form.Item style={props.id ? {} : {display:"none"}} label="Thi gian thc hin ">
            <DatePicker              
              style={{width:"60%", float:"left"}}
              value={props.thoiGianThucHien}
              onChange={(event) => {
                props.updateData({
                  thoiGianThucHien: event
                });
              }}
              format="DD/MM/YYYY"
              placeholder="Chn thi gian thc hin"
            />
            <TimePicker
              style={{width:"40%",float:"left"}}
              value={props.thoiGianThucHien}
              onChange={(event) => {
                props.updateData({
                  thoiGianThucHien: event
                });
              }}
            />
            {props.checkValidate && (props.thoiGianBanGiao && props.thoiGianThucHien && props.thoiGianBanGiao > props.thoiGianThucHien ? <div className="validate">Thi gian thc hin khng c nh hn thi gian nhn</div> : null)}
          </Form.Item>
        </div>
      </Form>
    </Modal>
  );
}

export default connect(
  state =>{
    return{
      auth: state.auth.auth,
      id: state.quanLyDoVai.id,
      dmDungCuId: state.quanLyDoVai.dmDungCuId,
      trangThai: state.quanLyDoVai.trangThai,
      khoaBanGiaoId: state.quanLyDoVai.khoaBanGiaoId,
      soLuong: state.quanLyDoVai.soLuong,
      departments: state.departments.departments || [],
      dmDungCu: state.dmDungcu.dmDungCu || [],
      thoiGianBanGiao: state.quanLyDoVai.thoiGianBanGiao ? moment(state.quanLyDoVai.thoiGianBanGiao) : null,
      thoiGianThucHien: state.quanLyDoVai.thoiGianThucHien ? moment(state.quanLyDoVai.thoiGianThucHien) : null,
      checkValidate: state.quanLyDoVai.checkValidate,
    }
  },
  {
    updateData: actionQuanLyDoVai.updateData,
    createOrEdit: actionQuanLyDoVai.createOrEdit,
    loadDetail: actionQuanLyDoVai.loadDetail,
    loadListDepartments: actionDepartments.loadList,
    loadListDmDungcu : actionDmDungCu.loadList,
  }
)(Form.create()(index))